import { productDetail } from '@/api/modules/product'
const actions = {
  GetDetailList ({ commit, state }, info) {
    return new Promise((resolve, reject) => {
      let postData = {
        id: info
      }
      productDetail(postData).then(res => {
        commit('GET_PRODUCT_DETAILS', res.data)
        console.log(1234,res.data)
        resolve(res)
      })
    }).catch(error => {
      reject(error)
    })
  }
}
export default actions
